.main {
	:global {
		.ant-upload-wrapper {
			display: flex;
			flex-direction: column-reverse;
			.ant-upload-list {
				.ant-upload-list-item-container {
					.ant-upload-list-item.ant-upload-list-item-error {
						color: var(--sText);

						.anticon {
							color: var(--sText);
						}

						.ant-upload-list-item-name {
							color: var(--sText);
						}
					}
				}
			}
		}
	}
}
