.table {
	.title {
		padding: 10px;
		font-size: 16px;
		font-weight: 700;
		font-style: normal;
		line-height: normal;
		text-align: center;
		border-bottom: 2px solid var(--sPrimary);
	}

	:global {
		.ant-table {
			table {
				border-collapse: collapse;
			}

			.ant-table-thead {
				tr {
					td,
					th {
						padding: 6px 10px !important;
						font-size: 14px;
						font-weight: 700;
						font-style: normal;
						line-height: normal;
						color: var(--sPrimary) !important;
						background: transparent !important;

						&::before {
							display: none;
						}
					}

					th:first-child {
						border-radius: 0 !important;
					}

					th:last-child {
						border-radius: 0 !important;
					}
				}
			}

			.ant-table-tbody {
				background: transparent !important;

				.ant-table-row {
					background: transparent !important;
					border-radius: 16px !important;

					&:not(:last-child) {
						border-bottom: solid transparent 5px !important;
					}

					.ant-table-cell {
						border: 0 !important;
						padding: 5px 15px !important;
						font-size: 14px;
						font-weight: 400;
						font-style: normal;
						line-height: 1;
						transition: all 0.2s ease;
						color: var(--sText) !important;
						background: #f5f7fb !important;

						&:first-child {
							border-radius: 10px 0 0 10px !important;
						}

						&:last-child {
							border-radius: 0 10px 10px 0 !important;
						}
					}

					&:hover .ant-table-cell {
						color: var(--sWhite) !important;
						background: var(--sPrimary) !important;
					}
				}
			}
		}
	}
}
