.svg-icon {
	&-success {
		path {
			fill: var(--sSuccess);
		}
	}

	&-danger {
		path {
			fill: var(--sDanger);
		}
	}

	&-primary {
		path {
			fill: var(--sPrimary);
		}
	}

	&-warning {
		path {
			fill: var(--sWarning);
		}
	}

	&-aqua {
		path {
			fill: darkturquoise;
		}
	}

	&-purple {
		path {
			fill: var(--sPurple);
		}
	}

	&-white {
		path {
			fill: var(--sWhite);
		}
	}

	&-blue {
		path {
			fill: var(--sBlue);
		}
	}
}
