* {
	border: 0;
	margin: 0;
	padding: 0;
	outline: none;
	box-sizing: border-box;
	font-family: var(--ff) !important;

	&::before,
	&::after {
		box-sizing: border-box;
	}
}

html,
body {
	color: var(--sText);
	background: var(--bgBody);
	font-family: var(--ff) !important;
	font-size: var(--fs);
	min-width: 1250px;
	overflow: hidden;
	height: 100vh;
	font-style: normal;
	// min-width: 375px;

	@media (max-width: 1250px) {
		overflow-x: auto;
	}
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
	-webkit-appearance: none;
	margin: 0;
}

#root {
	overflow-y: auto;
	display: block;
	height: 100vh;
	min-height: 100vh;
	max-height: 100vh;
}

.link {
	color: var(--sPrimary);
	font-style: normal;
	font-weight: 400;
	font-size: 14px;
	line-height: 129%;
	text-decoration-line: underline;
	color: #325ecd;
	cursor: pointer;
	transition: all 0.3s linear;

	&:hover {
		color: var(--sPrimaryDanger);
		text-decoration: underline;
	}
}

.default-link {
	font-style: normal;
	font-weight: 400;
	font-size: 15px;
	line-height: 133%;
	text-decoration-line: underline;
	transition: all 0.3s linear;
	color: var(--sPrimary);
	cursor: pointer;

	&:hover {
		text-decoration: none;
	}
}

.text-center {
	text-align: center !important;
}

.text-left {
	text-align: left !important;
}

.text-right {
	text-align: right !important;
}

.d-ib {
	display: inline-block;
}

.d-b {
	display: block;
}

.f-center {
	display: flex;
	align-items: center;
	justify-content: center;
}

.d-f {
	display: flex;
}

.f-col {
	flex-direction: column;
}

.jc-s {
	justify-content: flex-start;
}

.jc-c {
	justify-content: center;
}

.jc-e {
	justify-content: flex-end;
}

.jc-sa {
	justify-content: space-around;
}

.jc-sb {
	justify-content: space-between;
}

.jc-se {
	justify-content: space-evenly;
}

.ai-s {
	align-items: flex-start;
}

.ai-c {
	align-items: center;
}

.ai-e {
	align-items: flex-end;
}

.pointer {
	cursor: pointer;
}

.dropdown-btn {
	margin: -5px -12px;
	padding: 5px 10px;
	display: flex;
	align-items: center;
	border-radius: 4px;
	background: #ff7f00;

	&.isPause {
		background: var(--sPrimary);
	}

	.icon {
		width: 14px;
		height: 16px;
		margin-right: 8px;
		display: flex;
		align-items: center;
		justify-content: center;

		svg {
			width: 100%;
			height: 100%;
		}
		path {
			fill: #fff;
		}
	}

	.text {
		margin: 0;
		color: #fff;
	}
}

.table-bottom {
	gap: 20px;
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: space-between;

	@media (max-width: 991px) {
		gap: 10px;
		flex-direction: column;
	}
}

.disabled {
	background: #e6e9ed !important;
	color: #000 !important;

	&.ant-checkbox-wrapper {
		background: transparent !important;
		span {
			color: #000 !important;
		}
	}

	.ant-select-selection-item {
		color: #000 !important;
	}

	&:hover {
		border-color: transparent !important;
	}
}

iframe {
	pointer-events: none;
}

.primary-color {
	color: var(--sPrimary);
}

.gray-color {
	color: var(--sGray);
}


.w100 {
	width: 100%;
}

.bold {
	font-weight: 700;
}