.sidebar {
	background-color: #fff;
	max-width: 300px;
	width: 100%;
	position: fixed;
	opacity: 0;
	pointer-events: none;
	right: -350px;
	top: 0;
	min-height: 100vh;
	height: 100vh;
	max-height: 100vh;
	z-index: 1000;
	box-shadow: 0px 0px 20px rgba(119, 123, 130, 0.26);
	transition: all 0.3s ease;

	&Inner {
		margin-top: 50px;
		height: calc(100% - 50px);
		width: 100%;
		overflow-y: auto;
		padding: 40px;
	}

	&Open {
		pointer-events: initial;
		right: 0px;
		opacity: 1;
	}

	&Close {
		position: absolute;
		right: 10px;
		top: 10px;
		width: 30px;
		height: 30px;
		cursor: pointer;
		transition: all 0.2s ease-in;

		span {
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%) rotate(45deg);
			display: block;
			height: 3px;
			border-radius: 3px;
			width: 100%;
			background: var(--sText);

			&:last-child {
				transform: translate(-50%, -50%) rotate(-45deg);
			}
		}

		&:hover {
			transform: rotate(90deg);
		}
	}

	&Data {
		margin-bottom: 30px;

		h2 {
			font-size: 24px;
			line-height: 1.5;
			color: var(--sText);
			margin-bottom: 10px;
		}

		p {
			font-size: 14px;

			span {
				&:first-child {
					font-weight: 700;
				}
			}
		}
	}
}

.menu {
	list-style: none;
	display: flex;
	flex-direction: column;

	&Link {
		padding: 3px 0;
		display: block;
		cursor: pointer;
		font-size: 16px;
		line-height: 1.5;
		color: var(--sText);
		transition: all 0.3s linear;
		text-decoration: underline;

		&:hover {
			text-decoration: underline;
			color: var(--sPrimary);
		}
	}
}

.sidebarOverload {
	background: rgba(0, 0, 0, 0.5);
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	transition: opacity 0.3s linear;
	opacity: 0;
	z-index: -1;
	pointer-events: none;

	&Open {
		opacity: 1;
		z-index: 999;
		pointer-events: all;
	}
}
