.table {
	position: relative;

	.headerFilter {
		display: flex;
		gap: 20px;
		justify-content: space-between;
		margin-bottom: 20px;

		.left {
			margin-right: auto;
		}

		.right {
			margin-left: auto;
		}

		@media (max-width: 1200px) {
			flex-direction: column-reverse;
		}
	}

	:global {
		.ant-table {
			.ant-table-measure-row td {
				padding: 0 !important;
				border: 0 !important;
			}

			.ant-table-container {
				border: 0 !important;
			}

			th,
			td {
				padding: 10px !important;
				font-size: 14px !important;
				font-weight: 400 !important;
				font-style: normal !important;
				line-height: 20px !important;
				border: 0.5px solid var(--tableBorder);
			}
		}

		@media (max-width: 991px) {
			.ant-table-cell-fix-left {
				position: static !important;
			}
		}

		.ant-table-thead {
			border: 0.5px solid var(--tableBorder);
			tr {
				td,
				th {
					color: var(--sPrimary) !important;
					background: var(--tableHeadBg) !important;
				}

				th:first-child {
					border-radius: 0 !important;
				}

				th:last-child {
					border-radius: 0 !important;
				}
			}

			.ant-table-cell-scrollbar {
				display: none;
			}
		}

		.ant-table-tbody {
			background: var(--tableBg) !important;
			border: 0.5px solid var(--tableBorder);

			.ant-table-row {
				transition: background 0.3s ease-in-out;

				&:nth-child(even) .ant-table-cell {
					background: var(--tableBg) !important;
				}

				&:nth-child(odd) .ant-table-cell {
					background: var(--tableOddBg) !important;
				}

				.ant-table-cell {
					color: var(--sText) !important;
				}

				&:hover .ant-table-cell {
					background: var(--tableOddBg) !important;
				}
			}
		}

		.ant-table-summary {
			background: var(--tableBg);
			border: 0 var(--borderColor) !important;

			background: transparent;
			tr {
				border: 0 var(--borderColor) !important;
				background: transparent;
				td {
					background: transparent;
					background: var(--tableBg) !important;
					border: 0 var(--borderColor) !important;
				}
			}
		}

		.ant-table-expanded-row {
			padding: 0 !important;

			& > .ant-table-cell {
				padding: 0 !important;

				& > .ant-table-expanded-row-fixed {
					padding-right: 0 !important;
					padding-left: 47px !important;
				}
			}
		}
	}
}

div {
	::-webkit-scrollbar {
		width: 6px;
		height: 10px;
	}

	::-webkit-scrollbar-track {
		border: transparent !important;
		background: var(--scrollBar);
	}

	::-webkit-scrollbar-thumb {
		border: transparent !important;
		background: var(--scrollThumb) !important;
		border-radius: 6px;
	}
}

.bottomItems {
	margin-top: 20px;
	display: flex;
	justify-content: space-between;
	width: 100%;
}
