.header {
	box-sizing: border-box;
	background: var(--bgHeader);
	border-bottom: 5px solid var(--sPrimary);

	&Inner {
		position: relative;
		min-height: 74px;
		height: 74px;
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	&Logo {
		padding-top: 5px;
		width: 125px;

		svg {
			width: 100%;
			height: 100%;
		}
	}

	&Center {
		position: absolute;
		left: 50%;
		gap: 14px;
		display: flex;
		align-items: center;
		transform: translateX(-50%);

		.icon {
			display: flex;
			width: 48px;
			height: 48px;
			justify-content: center;
			align-items: center;
			flex-shrink: 0;
		}

		.content {
			display: flex;
			flex-direction: column;

			strong {
				font-size: 20px;
				font-style: normal;
				font-weight: 400;
				line-height: 28px;
			}

			span {
				font-size: 14px;
				font-style: normal;
				font-weight: 400;
				line-height: 20px;
			}
		}
	}

	&Right {
		gap: 40px;
		display: flex;
		align-items: center;
	}

	&Search {
		width: 32px;
		height: 32px;
		display: flex;
		align-items: center;
		justify-content: center;
		cursor: pointer;
		transition: all 0.3s ease;

		&:active {
			transform: scale(0.9);
		}
	}

	&ToggleIcon {
		cursor: pointer;
		min-width: 26px;
		width: 26px;
		height: 26px;
		display: flex;
		align-items: center;
		justify-content: center;
	}
}
