.main {
	:global {
		.ant-upload-wrapper {
			.ant-upload-list {
				.ant-upload-list-item {
					&.ant-upload-list-item-error {
						border-color: var(--borderColor);
					}
				}
			}
			.ant-upload-list-item-actions {
				gap: 10px;
				display: flex;
				align-items: center;
				justify-content: center;

				a {
					display: flex;
					align-items: center;
					justify-content: center;
				}
			}
		}
	}
}
