.footer {
	background: var(--bgFooter);
	width: 100%;

	.inner {
		height: 86px;
		display: flex;
		align-items: center;
		justify-content: space-between;

		.logo {
			width: 125px;

			svg {
				width: 100%;
			}
		}

		.socials {
			gap: 30px;
			display: flex;
			align-items: center;
			justify-content: flex-end;

			.item {
				color: var(--sWhite);
				font-size: 15px;
				font-style: normal;
				font-weight: 400;
				line-height: 28px;
				display: flex;
				gap: 7px;
				align-items: center;
				text-decoration: none;

				.icon {
					min-width: 32px;
					width: 32px;
					height: 32px;
					display: flex;
					align-items: center;
					justify-content: center;
				}

				&:hover {
					.label {
						text-decoration: underline;
					}
				}
			}
		}
	}
}
