.iconWithContent {
	gap: 10px;
	display: inline-flex;
	align-items: center;

	&.hasRightIcon {
		display: flex;
	}

	svg {
		width: 20px !important;
		height: 20px !important;
	}

	.rightIcon {
		margin-left: auto;
		width: 24px;
		height: 24px;
		display: flex;
		align-items: center;
		justify-content: center;

		svg {
			width: 24px !important;
			height: 24px !important;
		}
	}
}
